  .block-border:nth-of-type(1) {
    grid-row: 1;
    grid-column: 3/-3;
  }
  
  .block-border:nth-of-type(2) {
    grid-row: -2;
    grid-column: 3/-3;
  }
  
  .block-border:nth-of-type(3) {
    grid-row: 3/5;
    grid-column: 1;
  }
  
  .block-border:nth-of-type(4) {
    grid-row: 3/5;
    grid-column: -2;
  }
  
  .block-border:nth-of-type(5) {
    grid-row: 2;
    grid-column: 2;
  }
  
  .block-border:nth-of-type(6) {
    grid-row: 2;
    grid-column: -3;
  }
  
  .block-border:nth-of-type(7) {
    grid-row: 5;
    grid-column: 2;
  }
  
  .block-border:nth-of-type(8) {
    grid-row: 5;
    grid-column: -3;
  }
  
  .block-meter:nth-of-type(9) {
    grid-row: 3/5;
    grid-column: 4;
  }
  
  .block-meter:nth-of-type(10) {
    grid-row: 3/5;
    grid-column: 6;
  }
  
  .block-meter:nth-of-type(11) {
    grid-row: 3/5;
    grid-column: 8;
  }
  
  .block-meter:nth-of-type(12) {
    grid-row: 3/5;
    grid-column: 10;
  }
  
  .block-meter:nth-of-type(13) {
    grid-row: 3/5;
    grid-column: 12;
    opacity: 0;
    -webkit-animation: blinky1 2s linear forwards;
            animation: blinky1 2s linear forwards;
  }
  
  .block-meter:nth-of-type(14) {
    grid-row: 3/5;
    grid-column: 14;
    opacity: 0;
    -webkit-animation: blinky1 2s 4s linear forwards;
            animation: blinky1 2s 4s linear forwards;
  }
  
  .block-meter:nth-of-type(15) {
    grid-row: 3/5;
    grid-column: 16;
    opacity: 0;
    -webkit-animation: blinky1 2s 8s linear forwards;
            animation: blinky1 2s 8s linear forwards;
  }
  
  .block-meter:nth-of-type(16) {
    grid-row: 3/5;
    grid-column: 18;
    opacity: 0;
    -webkit-animation: blinky2 3s 14s linear infinite;
            animation: blinky2 3s 14s linear infinite;
  }
  
  @-webkit-keyframes blinky1 {
